<template>
  <div class="addchieve">
    <el-card class="title">
      <img src="../../../assets/icons/left.png" />
      <span @click="$emit('InvoiceOffFN')">返回开票详情</span>
      <b>业绩单详细</b>
    </el-card>
    <div class="scoll_context">
      <el-card class="infoSuccess_card" v-for="(item,index) in arrsFn" :key="index">
        <div slot="header" class="clearfix">
          <span>认购信息</span>
        </div>
        <el-row :gutter="6" class="infoSuccess">
          <el-col :span="12">
            <div
              :class="item.key==='picUrls'||item.key==='picUrls2'||item.key==='eleUrls'?'itemImg':'item'"
              v-for="(item,index) in item.one"
              :key="index"
            >
              <div>{{item.title}}:</div>
              <div v-if="item.key==='picUrls'||item.key==='picUrls2'||item.key==='eleUrls'">
                <img
                  @click="imgFn(items)"
                  class="imgs"
                  v-for="(items) in item.text"
                  :key="items"
                  :src="items"
                />
              </div>
              <div v-else>{{item.text}}</div>
            </div>
          </el-col>
          <el-col :span="12">
            <div
              :class="item.key==='picUrls'||item.key==='picUrls2'||item.key==='eleUrls'?'itemImg':'item'"
              v-for="(item,index) in item.two"
              :key="index"
            >
              <div>{{item.title}}:</div>
              <div v-if="item.key==='picUrls'||item.key==='picUrls2'||item.key==='eleUrls'">
                <img
                  @click="imgFn(items)"
                  class="imgs"
                  v-for="(items) in item.text"
                  :key="items"
                  :src="items"
                />
              </div>
              <div v-else>{{item.text}}</div>
            </div>
          </el-col>
        </el-row>
      </el-card>
      <!-- 成交单操作按钮 -->
      <template >
       <el-card body-style="text-align: center;">
            <el-button type="danger" @click="addAlertFn"  icon="el-icon-plus">添加业绩</el-button>
       </el-card>
      </template>
      <!-- 成交单操作按钮 -->
    </div>
    <!-- 添加业绩单录入 -->
    <addchieveAlert :dialogVisible="addAlert"/>
    <!-- 添加业绩单录入 -->
  </div>
</template>
<script>
import { dateTime } from "@/utils/common";
import addchieveAlert from './addchieveAlert'
export default {
    components:{
        addchieveAlert,
    },
    props:{
        infoSuObj:{
            type:Object
        }
    },
  data() {
    return {
      arrs: [
        {
          title: "成交单详细",
          one: [
            {
              title: "楼盘",
              text: "",
              key: "projectName"
            },
            {
              title: "门店",
              text: "",
              key: "departmentName"
            },
            {
              title: "经纪人",
              text: "",
              key: "agentName"
            },
            {
              title: "客户姓名",
              text: "",
              key: "customName"
            },
            {
              title: "客户电话",
              text: "",
              key: "customerPhone"
            },
            {
              title: "认购日期",
              text: "",
              key: "tradeTime"
            },
            {
              title: "房号",
              text: "",
              key: "roomNo",
              lable: "号"
            },
            {
              title: "面积",
              text: "",
              key: "area",
              lable: "m*2"
            },
            {
              title: "成交金额",
              text: "",
              key: "sumMoney",
              lable: "元"
            },
            {
              title: "税后金额",
              text: "",
              // key: "commSumMoney",sumCommission
              key: "commSumMoney",
              lable: "元"
            },
           
            {
              title: "认购备注",
              text: "",
              key: "tradeRemark"
            },
            {
              title: "成交凭证",
              text: "",
              key: "picUrls"
            }

            // projectName: 32, //楼盘ID
            // departmentName: 1407, //门店ID
            // agentName: "f7ea60bd2d4311ea98ab70106f4a153c", //经纪人ID
            // customerName: "撒陈凯顶", //客户姓名
            // customerPhone: "13981722508", //客户电话
            // tradeTime: "2018-12-9", //认购时间
            // roomNO: 1, //房屋号
            // area: 1223, //房屋面积
            // sumMoney: 1256, //总金额
            // // signTime: "", //签约日期
            // cycleDays: "", //结佣周期，天数
            // preCommission: "", //前佣
            // sufCommission: "", //后佣
            // cash: "", //现金奖
            // sumCommission: 0.0, //总佣金，总佣金 包括现金奖 前佣 后佣
            // // revenue: "", //营收
            // picUrl: [], //成交信息图片数组URL
            // // picUrl2: [], //业绩信息图片数据URL
            // isElectron: 1, //是否刷电商 1是 0否
            // elePicUrl: [], //刷电商图片数据URL
            // commSumMoney: 263, //用于计算佣金的成交总金额，为null时跟sumMoney一致
            // groupSumMoney: 36, //团购金额
            // groupCanMoney: 39, //团购费可结
            // cardMoney: "123", //刷卡金额
            // remark: "213" //备注
          ],
          two: [
               {
              title: "团购费实收",
              text: "",
              key: "groupSumMoney",
              lable: "元"
            },
            {
              title: "团购费可结",
              text: "",
              key: "groupCanMoney",
              lable: "元"
            },
            {
              title: "团购凭证",
              text: "",
              key: "eleUrls"
            },
            {
              title: "创建人",
              text: "",
              key: "createrName"
            },
            {
              title: "创建时间",
              text: "",
              key: "createTime"
            },
            {
              title: "状态",
              text: "",
              key: "statusStr"
            },
            {
              title: "提交时间",
              text: "",
              key: "commitTime"
            },
            {
              title: "审核人",
              text: "",
              key: "checkName"
            },
            {
              title: "审核时间",
              text: "",
              key: "checkTime"
            },
            {
              title: "原因",
              text: "",
              key: "reason"
            }
          ]
        }
      ],
      //业绩单录入弹窗
      addAlert:{
          trunoff:false,
          data:null
      }
    };
  },
  methods:{
    addAlertFn(){
        this.addAlert.trunoff=true
    }  
  },
  computed:{
      arrsFn(){
       let data = [];
      this.arrs.forEach(item => {
        data = [...data, ...item.one, ...item.two];
      });
      let type={
        '1':'已提交',
        '2':'待审核',
        '4':'已拒绝',
        '3':'已通过'
      }
      data.forEach(item => {
        if (item.lable != undefined) {
             item.text =
            this.infoSuObj[item.key] == null ||
            this.infoSuObj[item.key] == undefined
              ? 0 + item.lable
              : this.infoSuObj[item.key] + item.lable;
        } else {
          if (item.key.indexOf("Time") != -1) {
            item.text = dateTime(this.infoSuObj[item.key]);
          } else if(item.title=='客户电话'){
              item.text=this.infoSuObj.preMobile+this.infoSuObj.midMobile+this.infoSuObj.sufMobile
          }
          // else if(item.title==='状态'){
          //   item.text=type[this.infoSuObj.checkStatus]
          // }
          else{
            item.text = this.infoSuObj[item.key];
          }
        }
      });
      this.addAlert.data=this.arrs
      return this.arrs;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "@/css/common.scss";
.infoSuccess_card{
    margin-top: 20px;
}
</style>