<template>
  <div class="addPic">
    <el-dialog
      class="addPic_top"
      title="成交单编辑"
      :visible.sync="dialogVisible.trunoff"
      width="86%"
      :before-close="handleClose"
      top="10px"
    >
      <el-card>
        <el-form :rules="rules" ref="fromData" :model="fromData" label-width="100px">
          <el-row>
            <el-col :span="8">
              <el-form-item prop="projectId" label="楼盘">
                <el-select v-model="fromData.projectId" placeholder="请选择楼盘">
                  <el-option
                    v-for="(item,index) in $store.getters.projectAll"
                    :label="item.projectName"
                    :value="item.projectId"
                    :key="index"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="value" label="门店">
                <el-autocomplete
                  class="inline-input"
                  v-model="value"
                  style="width:190px;"
                  :fetch-suggestions="querySearch"
                  placeholder="请选择门店"
                  @select="handleSelect"
                  @keydown.native="keydownFn"
                ></el-autocomplete>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="value2" label="经纪人">
                <!-- <el-select v-model="fromData.agentId" placeholder="请选择楼盘">
                <el-option
                  v-for="(item,index) in $store.getters.projectAll"
                  :label="item.projectName"
                  :value="item.projectId"
                  :key="index"
                ></el-option>
                </el-select>-->
                <el-autocomplete
                  class="inline-input"
                  v-model="value2"
                  style="width:190px;"
                  :fetch-suggestions="querySearchagentId"
                  placeholder="请选择经纪人"
                  @select="handleSelectagentId"
                  @keydown.native="keydownFnagentId"
                  @click="checkDowm"
                  :disabled="value==''||fromData.deptId==''?true:false"
                ></el-autocomplete>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item prop="customerName" label="客户姓名">
                <el-input v-model="fromData.customerName" placeholder="请输入客户姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="customerPhone" label="客户电话">
                <el-input v-model.number="fromData.customerPhone" placeholder="请输入客户电话"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="tradeTime" label="认购日期">
                <el-date-picker
                  format="yyyy-mm-dd"
                  v-model="fromData.tradeTime"
                  type="date"
                  placeholder="请选择认购日期"
                ></el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item prop="roomNO" label="房号">
                <el-input v-model.number="fromData.roomNO" placeholder="请输入房号"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="area" label="面积">
                <el-input v-model.number="fromData.area" placeholder="请输入面积"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="sumMoney" label="成交金额">
                <el-input v-model.number="fromData.sumMoney" placeholder="请输入成交金额"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item prop="commSumMoney" label="税后金额">
                <el-input v-model.number="fromData.commSumMoney" placeholder="请输入税后金额"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="isElectron" label="是否有团购">
                <el-switch v-model="fromData.isElectron"></el-switch>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <!-- 团购费 -->
              <template v-if="fromData.isElectron">
                <el-form-item prop="groupSumMoney" label="团购费实收">
                  <el-input v-model.number="fromData.groupSumMoney"></el-input>
                </el-form-item>
              </template>
            </el-col>
          </el-row>
          <template v-if="fromData.isElectron">
            <el-row>
              <el-col :span="8">
                <el-form-item prop="groupCanMoney" label="团购费可结">
                  <el-input v-model.number="fromData.groupCanMoney"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="24">
                <el-form-item label="团购费凭证">
                  <!-- <el-input type="textarea" v-model="fromData.elePicUrl"></el-input> -->
                  <upPictrue :imgUrl="fromData.elePicUrl" ref="upPictrueEle" />
                </el-form-item>
              </el-col>
            </el-row>
          </template>
          <!-- 团购费 -->
          <el-row>
            <el-col :span="24">
              <el-form-item label="添加凭证">
                <!-- <el-input type="textarea" v-model="fromData.picUrl"></el-input> -->
                <upPictrue :imgUrl="fromData.picUrl" ref="upPictrue" />
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <el-form-item label="备注">
                <el-input type="textarea" v-model="fromData.remark"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-form-item>
              <el-button style="float: right;" type="primary" @click.native="submitForm">保存</el-button>
            </el-form-item>
          </el-row>
        </el-form>
      </el-card>
    </el-dialog>
  </div>
</template>
<script>
import { deptList } from "@/api/business/management";
import { userList } from "@/api/business/commission";
import { editTicket, commitTicket } from "@/api/business/Picturepreview";
import { validatePass, dateTime } from "@/utils/common";
import upPictrue from "@/components/common/upPictrue";
export default {
  components: {
    upPictrue
  },
  props: {
    dialogVisible: {
      type: Object
    },
    infoSuccessData: {
      type: Object
    }
  },
  data() {
    return {
      selectQuery: {
        //门店传参
        departmentName: "",
        pageSize: 10,
        pageNo: 1
      },
      userListQuery: {
        //经纪人传参
        pageSize: 115,
        pageNo: 1,
        key: "",
        deptId: ""
      },
      //数据匹配
      fromData: {
        //成交单录入添加的参数
        projectId: 32, //楼盘ID
        deptId: 1407, //门店ID
        agentId: "f7ea60bd2d4311ea98ab70106f4a153c", //经纪人ID
        customerName: "撒陈凯顶", //客户姓名
        customerPhone: "13981722508", //客户电话
        tradeTime: "2018-12-9", //认购时间
        roomNO: 1, //房屋号
        area: 1223, //房屋面积
        sumMoney: 1256, //总金额
        // signTime: "", //签约日期
        // cycleDays: "", //结佣周期，天数
        // preCommission: "", //前佣
        // sufCommission: "", //后佣
        // cash: "", //现金奖commSumMoney
        sumCommission: 0.0, //总佣金，总佣金 包括现金奖 前佣 后佣
        // revenue: "", //营收
        picUrl: [], //成交信息图片数组URL
        // picUrl2: [], //业绩信息图片数据URL
        isElectron: 1, //是否刷电商 1是 0否
        elePicUrl: [], //刷电商图片数据URL
        commSumMoney: 263, //用于计算佣金的成交总金额，为null时跟sumMoney一致
        groupSumMoney: 36, //团购金额
        groupCanMoney: 39, //团购费可结
        cardMoney: "123", //刷卡金额
        remark: "213" //备注
        // isAgency: "" //是否代理成交单，1：是 0或null：否
      },
      ok: {
        ticketId: 562,
        agentId: "f7ea60bd2d4311ea98ab70106f4a153c",
        area: "12356",
        cash: 0.0,
        commSumMoney: "263.00",
        customerName: "陈恺",
        customerPhone: "13981722508",
        cycleDays: 0,
        deptId: 1407,
        elePicUrl: [
          "https://yunjinji.oss-cn-shenzhen.aliyuncs.com/202003/cdc114f1-7881-4ea4-945c-1aee1c9cade0.jpg"
        ],
        groupCanMoney: "36.00",
        groupSumMoney: "36.00",
        isAgency: 1,
        isElectron: 1,
        picUrl: [
          "https://yunjinji.oss-cn-shenzhen.aliyuncs.com/202003/a784942f-b170-4c17-8c68-df985c54774a.jpg"
        ],
        preCommission: 0.0,
        projectId: 32,
        remark: "砍砍价就能玩",
        revenue: 0.0,
        roomNO: "1256",
        sufCommission: 0.0,
        sumCommission: 0.0,
        sumMoney: "1256.0",
        tradeTime: "2020-03-02",
        appId: "prof",
        departmentId: 999,
        model: "phone",
        role: 4,
        system: "android",
        uid: "63dd4732676f11ea978570106f4a153c"
      },

      fromDatas: {
        //成交单录入添加的参数
        projectId: 123, //楼盘ID
        deptId: 122, //门店ID
        agentId: "1232321321", //经纪人ID
        customerName: "阿萨", //客户姓名
        customerPhone: "124548787", //客户电话
        tradeTime: "45632486345", //认购时间
        roomNO: "30", //房屋号
        area: "123", //房屋面积
        sumMoney: "123", //总金额
        signTime: "111111111123", //签约日期
        cycleDays: 2, //结佣周期，天数
        preCommission: 20, //前佣
        sufCommission: 20, //后佣
        cash: 20, //现金奖
        sumCommission: 20, //总佣金，总佣金 包括现金奖 前佣 后佣
        revenue: "62", //营收
        picUrl: [], //成交信息图片数组URL
        // picUrl2: [], //业绩信息图片数据URL
        isElectron: 1, //是否刷电商 1是 0否
        // elePicUrl: [], //刷电商图片数据URL
        commSumMoney: "11", //用于计算佣金的成交总金额，为null时跟sumMoney一致
        groupSumMoney: "12", //团购金额
        groupCanMoney: "12", //团购费可结
        cardMoney: "123", //刷卡金额
        remark: "12222", //备注
        isAgency: 1 //是否代理成交单，1：是 0或null：否
      },
      //数据匹配
      rules: {
        projectId: [{ required: true, message: "请选择楼盘", trigger: "blur" }],
        deptId: [{ required: true, message: "请选择经纪人", trigger: "blur" }],
        agentId: [{ required: true, message: "请选择经纪人", trigger: "blur" }],
        customerName: [
          { required: true, message: "请输入客户姓名", trigger: "blur" }
        ],
        customerPhone: [
          {
            required: true,
            message: "请输入客户电话",
            // type: "number",
            trigger: "blur"
          },
          {validator: validatePass,trigger:'change'}
        ],
        tradeTime: [
          {
            required: true,
            type: "date",
            message: "请输入认购时间",
            trigger: "blur"
          }
        ],
        roomNO: [
          { required: true, message: "请输入房号", trigger: "blur" }
          // {validator: validatePass,trigger:'change'}
        ],
        area: [
          { required: true, message: "请输入面积", trigger: "blur" },
          {validator: validatePass,trigger:'change'}
        ],
        sumMoney: [
          { required: true, message: "请输入成交金额", trigger: "blur" },
          {validator: validatePass,trigger:'change'}
        ],
        sumCommission: [
          { required: true, message: "请输入税后金额", trigger: "blur" },
          {validator: validatePass,trigger:'change'}
        ],
        groupSumMoney: [
          { required: true, message: "请输入团购费实收", trigger: "blur" },
          {validator: validatePass,trigger:'change'}
        ],
        groupCanMoney: [
          { required: true, message: "请输入团购费可结", trigger: "blur" },
          {validator: validatePass,trigger:'change'}
        ]
      },
      value: "", //门店虚值
      value2: "" //经纪人虚值
    };
  },
  methods: {
    handleClose(done) {
      done();
    },
    // 保存
    submitForm() {
      let data = JSON.parse(JSON.stringify(this.fromData));
      data.isElectron = data.isElectron ? 1 : 0;
      if(data.isElectron==1){
        data.elePicUrl= this.$refs.upPictrueEle.urls  //团购费凭证
      }else{
        data.groupSumMoney=0
        data.groupCanMoney=0
      }
      data.tradeTime = dateTime(data.tradeTime, 0);
      data.picUrl= this.$refs.upPictrue.urls  //成交凭证
      // data.picUrl2= this.$refs.upPictrueEle.urls  //业绩凭证
      this.$refs.fromData.validate(valid => {
        if (valid) {
          // console.log();
          // alert('submit!');
          // this.resetFrom()
          editTicket({
            ...data,
            ticketId: this.infoSuccessData.ticketId
          }).then(res => {
            if (res.data.code === "000") {
              this.$confirm("已保存, 是否提交?", "提示", {
                confirmButtonText: "是",
                cancelButtonText: "否",
                type: "warning"
              }).then(() => {
                commitTicket({
                  ticketId: this.infoSuccessData.ticketId
                }).then(res => {
                  if(res.data.code==='000'){
                    this.$message.success('提交成功')
                    this.resetFrom()
                  }
                });
              })
              .catch(()=>{
                this.resetFrom()
              })
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //门店
    querySearch(queryString, cb) {
      this.selectQuery.departmentName = queryString;
      this.requestList(cb);
    },
    handleSelect(item) {
      this.fromData.deptId = item.departmentId;
      this.userListQuery.deptId = item.departmentId;
    },
    keydownFn() {
      if (this.value.length === 1 || this.value.length === 0) {
        this.fromData.deptId = 0;
      }
    },
    requestList(cb) {
      //门店列表
      deptList(this.selectQuery).then(res => {
        if (res.data.code === "000") {
          this.stores = res.data.bizData.items;
          if (this.stores.length === 0 || this.stores == null) {
            cb([{}]);
          } else {
            var resdwda = this.stores.map(item => {
              item.value = item.departmentName;
              return item;
            });
            cb(resdwda);
          }

          // 调用 callback 返回建议列表的数据
        }
      });
    },
    //门店
    //经纪人
    querySearchagentId(queryString, cb) {
      this.userListQuery.key = queryString;
      this.requestListagentId(cb);
    },
    handleSelectagentId(item) {
      this.fromData.agentId = item.uid;
    },
    keydownFnagentId() {
      if (this.values.length === 1 || this.values.length === 0) {
        this.fromData.agentId = "";
      }
    },
    requestListagentId(cb) {
      //门店列表
      userList(this.userListQuery).then(res => {
        if (res.data.code === "000") {
          let dataArr = res.data.bizData.items;
          if (dataArr.length === 0 || dataArr == null) {
            cb([{}]);
          } else {
            var resdwda = dataArr.map(item => {
              item.value = item.name;
              return item;
            });
            cb(resdwda);
          }

          // 调用 callback 返回建议列表的数据
        }
      });
    },
    checkDowm() {},
    //经纪人
    //重置
    resetFrom() {
      this.$parent.onSubmit()
      this.$parent.handleClick(this.$store.getters.tickta)
      this.$refs.fromData.resetFields();
      this.dialogVisible.trunoff=false
    }
  },
  watch: {
    infoSuccessData: {
      handler(val, old) {
        for (let key in this.fromData) {
          if (key === "isElectron") {
            //是否团购
            this.fromData[key] = this.infoSuccessData[key] ? true : false;
            continue;
          }
          if (key === "roomNO") {
            //房号
            this.fromData[key] = this.infoSuccessData.roomNo;
            continue;
          }
          if (key === "customerName") {
            //客户姓名
            this.fromData[key] = this.infoSuccessData.customName;
            continue;
          }
          if (key === "customerPhone") {
            //客户电华
            this.fromData[key] =
              this.infoSuccessData.preMobile +
              this.infoSuccessData.midMobile +
              this.infoSuccessData.sufMobile;
            continue;
          }
          if (key === "picUrl") {
            //凭证
            this.fromData[key] = this.infoSuccessData.picUrls;
            continue;
          }
          if (key === "elePicUrl") {
            //团购凭证
            this.fromData[key] = this.infoSuccessData.eleUrls;
            continue;
          }
          if (key == "remark") {
            //备注
            this.fromData[key] = this.infoSuccessData.tradeRemark;
            continue;
          }
          this.value = this.infoSuccessData.departmentName;
          this.value2 = this.infoSuccessData.agentName;
          this.fromData.agentId = this.infoSuccessData.agentId;
          this.fromData.deptId = this.infoSuccessData.departmentId;
          this.fromData.projectId = this.infoSuccessData.projectId;
          this.fromData[key] = this.infoSuccessData[key];
        }
      },
      deep: true
    }
  }
};
</script>
<style lang="scss">
.addPic .addPic_top .el-dialog {
  background-color: rgb(245, 241, 241) !important;
  .el-dialog__body {
    padding-top: 0;
  }
}
</style>